*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: "PT Sans", sans-serif;
  font-size: 14px;
  line-height: 26px;
  letter-spacing: 0.01em;
  text-align: center;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
h2 {
  font-size: 18px;
  line-height: 26px;
  letter-spacing: 0.02em;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
.container {
  display: block;
  width: 100%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.visually_hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1280px) {
  .container {
    max-width: 1200px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  }
}
.advantages {
