  padding: 8px 30px 23px;
  text-align: left;
  list-style: none;
}
.advantages__item {
  position: relative;
}
.advantages__item-img {
  position: absolute;
  top: -28%;
  left: -3%;
}
.advantages__item-img--souvenirs {
  top: -32px;
}
.advantages__item-img--food {
  top: -23px;
  left: -10px;
}
