.main-nav {
  position: relative;
}
.main-nav__toggle-close {
  position: absolute;
  top: 7%;
  right: 5%;
  width: 25px;
  height: 25px;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: rgba(225, 225, 225, 0);
}
.main-nav__toggle-close::before,
.main-nav__toggle-close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 27px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle-close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav__toggle-close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.main-nav__toggle-close:hover::before,
.main-nav__toggle-close:hover::after {
  background-color: #cde1ed;
}
.main-nav__toggle-close:active::before,
.main-nav__toggle-close:active::after {
  background-color: #a7cae0;
}
.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__item {
  height: 56px;
}
.main-nav__link {
  display: block;
  width: 100%;
  height: 56px;
  padding: 15px;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
  background-color: #81b3d2;
  color: #ffffff;
}
.main-nav__link:hover {
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #86b2cd;
  background-color: #669ec0;
}
.main-nav__link:active {
  color: #88b6d1;
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .main-nav__toggle-close {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  .main-nav__item {
    width: 20%;
  }
  .main-nav__item:nth-child(2) {
    margin-right: auto;
  }
  .main-nav__link {
    font-size: 14px;
    border: none;
    color: #000000;
    background-color: #ffffff;
  }
  .main-nav__link:hover {
    border: none;
    color: #81b3d2;
    background-color: #ffffff;
  }
  .main-nav__link:active {
    color: #b2b2b2;
  }
}
